import React from 'react';
import { useTheme, useMediaQuery, Grid, Container, Box, Typography, Stepper, Step, StepContent, StepButton } from '@mui/material';

const steps = [
  {
    label: 'Fill in exchange details',
    description: 'Choose an exchange pair from the list. There are more than 50,000 of them, so you will definitely find one that you want.',
  },
  {
    label: 'Make a deposit',
    description: 'Check all exchange details, get an estimated transaction time, and send your funds to our one-time deposit address.',
  },
  {
    label: 'Receive crypto',
    description: 'Enjoy the ChangeNOW magic. We\'ll find the best possible exchange rate, swap your assets, and send them back to you in a few minutes.',
  },
];

const Exchange = (): JSX.Element => {
  const [activeStep, setActiveStep] = React.useState(0);

  const theme = useTheme();

  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });

  const handleStep = (step: number) => () => {
    setActiveStep(step);
  };

  return (
    <Container sx={{ marginY: 5, paddingBottom: 2 }}>
      <Grid
        container
        spacing={4} direction={isMd ? 'row' : 'column'}
      >
        <Grid
          item
          container
          xs={12}
          md={7}
        >
          <Box>
            <Typography
              variant="h4"
              sx={{
                marginBottom: 4,
                fontWeight: 700,
                textTransform: 'uppercase'
              }}
              data-aos={isMd ? 'fade-left' : 'fade-up'}
            >
              {'Limitless '}
              <Typography color="primary" variant="inherit" component="span">
                {'Crypto Exchange'}
              </Typography>
            </Typography>
            <Typography
              component="p"
              sx={{
                marginBottom: 4,
                fontWeight: 700,
              }}
              data-aos={'fade-up'}
            >
              {'Fast Crypto Swaps, Free of Custody.'}
            </Typography>
            <Stepper
              nonLinear
              activeStep={activeStep}
              orientation="vertical"
              data-aos={'fade-up'}
            >
              {steps.map((step, index) => (
                <Step key={step.label}>
                  <StepButton onClick={handleStep(index)}>
                    {step.label}
                  </StepButton>
                  <StepContent>
                    {step.description}
                  </StepContent>
                </Step>
              ))}
            </Stepper>
          </Box>
        </Grid>
        <Grid
          item
          container
          xs={12}
          md={5}
        >
          <Box
            component={'iframe'}
            id={'iframe-widget'}
            src={
              'https://changenow.io/embeds/exchange-widget/v2/widget.html?FAQ=false&amount=1&backgroundColor=' + theme.palette.background.paper.substring(1) + '&darkMode=' + (theme.palette.mode === 'light' ? 'false' : 'true') + '&from=eth&horizontal=false&lang=en-US&link_id=b481a387cc179c&locales=false&logo=false&primaryColor=' + theme.palette.primary.main.substring(1) + '&to=maticmainnet&toTheMoon=false'
            }
            sx={{
              width: '100%',
              minHeight: '375px',
              border: 'none'
            }}
            data-aos={isMd ? 'fade-right' : 'fade-up'}
          >
          </Box>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Exchange;
